<style>
.img-desk-sejarah{
	width: 50% !important;
}
.pre-formatted {
  white-space: pre;
}
	.timeline {
  margin: 50px 0;
  list-style-type: none;
  display: flex;
  padding: 0;
  text-align: center;
}
.timeline li {
  transition: all 200ms ease-in;
}
.timestamp {
	padding: 0px 40px;
  display: flex;
  justify-content: center;
  position: relative;
  transition: all 200ms ease-in ;
  z-index: 999;
  color: #fff;
  top: 14px;
}
.timestamp span {
  font-weight: 700;
}
.status {
  padding: 0px 40px;
  display: flex;
  justify-content: center;
  border-top: 6px solid #909592;
  position: relative;
  transition: all 200ms ease-in ;
}
  
.status span {
  font-weight: 600;
  padding-top: 45px;
    padding-left: 10px;
    padding-right: 10px;
}
.status span:before {
	content: '';
	width: 80px;
	height: 80px;
	background-color: #2c4739;
	border-radius: 80px;
	border: none;
	position: absolute;
	top: -42px;
	left: 38%;
	transition: all 200ms ease-in;
}
.status span:hover:before{
	background-color: #0aa690;
}
.swiper-control {
  text-align: right;
}

.swiper-container {
  width: 100%;
  height: 250px;
  margin: 50px 0;
  overflow: hidden;
  padding: 0 20px 30px 20px;
}
.swiper-slide {
  width: 200px;
  text-align: center;
  font-size: 18px;
}
.swiper-slide:nth-child(2n) {
  width: 40%;
}
.swiper-slide:nth-child(3n) {
  width: 20%;
}
</style>